@font-face {
  font-family: Avenir-Book;
  src: url(fonts/Avenir-Book.ttf);
  font-weight: 300;
}
  
@font-face {
  font-family: Avenir-Heavy;
  src: url(fonts/Avenir-Heavy.ttf);
  font-weight: 900;
}

body {
  font-family: Avenir-Book, sans-serif;
  font-weight: 300;
}

.search-form {
  margin-top: 20px;
}

.search {
  color: #3d4e61;
  width: 100%;
  background-image: url(images/Search.svg);
  background-repeat: no-repeat;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  border-radius: 4px;
  background-position: 26px 25px;
  background-size: 19px;
  height: 70px;
  font-size: 16px;
  padding: 25px 62px;
  line-height: 22px;
}

.search--small {
  background-position: 12px 14px;
  background-size: 11px;
  height: 42px;
  font-size: 14px;
  padding: 10px 0 12px 33px;
  line-height: 19px;
}

.search::placeholder {
  font-weight: 400px;
  font-family: Avenir-Book, sans-serif;
}

.search:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.search:focus {
  background-image: url(./images/Search.svg), -webkit-linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-family: Avenir-Heavy, sans-serif;
  font-weight: 900;
}
